<template>
	<view>
		<view style="position: fixed;
		top:0;
		left:0;
		width: 100%;
		z-index: -1;
		height: 100%;
		background-color: #f0f0f0;
		"></view>
		<kt-nav-bar id="navBar" :title="room.name" :showBack="true">
			<follow-btn :ovoUserInfo="room.otherUser" />
		</kt-nav-bar>
		<scrollViewBox 
		ref="scrollViewBox"
		:chatInputHeight="chatInputHeight"
		 :height="'calc(100vh - ' + navBarHeight + 'px - ' + chatInputHeight + 'px)'"
			:room="room"></scrollViewBox>
		<view style="position: fixed;width:100%;bottom:0;left:0;">
			<chatInput id="chatInput" @change="chatInputChange" @publish="chatInputPublish" :room="room"></chatInput>
		</view>

	</view>
</template>

<script>
import scrollViewBox from './inChatBody/scrollViewBox.vue';
import chatInput from './inChatBody/chatInput.vue';

export default {
	components: {
		scrollViewBox,
		chatInput
	},
	data() {
		return {
			room: {
				id: null,
				typeCode: "privateChat",
				otherUser: null,
				name: null,
			},
			/**
			 * 聊天内容
			 */
			bodyData: {
				content: []
			},
			ovoUserSelf: {},
			navBarHeight: 0,
			chatInputHeight: 0,
			publishNum: 0,
		}
	},
	onLoad(options) {
		this.ovoUserInfo = this.$ovo.getSelf();

		if (options.roomId!=null) {
			// 如果有传roomId，就执行获取房间信息，因为可能是直接找人私聊来的
			this.room = uni.getStorageSync("roomId:" + options.roomId + ":room");
			if (uni.getStorageSync("roomId:" + options.roomId + ":room") == "") {
				this.getRoomById(options.roomId);
			}
			return false;
		}

		if(options.roomTypeCode!=null){
			this.room.typeCode = options.roomTypeCode;
			var otherUserId = options.otherUserId;
			this.room.otherUser = uni.getStorageSync("userId:" + otherUserId + ":ovoUserInfo");
			this.room.name = this.room.otherUser.user.nickname;
			this.getPrivateChatRoomByOtherUserId(otherUserId);
			this.$forceUpdate();
			return false;
		}

	},
	mounted() {
		uni.createSelectorQuery().in(this).select('#navBar').boundingClientRect((rect) => {
			this.navBarHeight = rect.height;
		}).exec();

		uni.createSelectorQuery().in(this).select('#chatInput').boundingClientRect((rect) => {
			this.chatInputHeight = rect.height;
		}).exec();
		setTimeout(() => {
			uni.createSelectorQuery().in(this).select('#chatInput').boundingClientRect((rect) => {
				this.chatInputHeight = rect.height;
			}).exec();
		}, 100)
	},
	methods: {
		getPrivateChatRoomByOtherUserId(otherUserId) {
			this.$kt.request.request({
				api: this.$api.business.ovo.chatRoom.getPrivateChatRoomByOtherUserId,
				data: {
					otherUserId
				},
				stateSuccess: (res) => {
					if(res.data!=null){
						this.room = res.data;
						console.log(JSON.stringify(this.room),"-=-=-=-=-");
						uni.setStorageSync("roomId:" + this.room.roomId + ":room", this.room);
						setTimeout(() => {
							this.$refs.scrollViewBox.getBodyData();
						}, 100);
						return null;
					}
				}
			});
		},
		chatInputChange() {
			uni.createSelectorQuery().in(this).select('#chatInput').boundingClientRect((rect) => {
				this.chatInputHeight = rect.height;
			}).exec();
			this.$refs.scrollViewBox.toBottom();
		},
		chatInputPublish(data) {
			if(this.room.id==null){
				this.room.id = data.roomId;
				this.getRoomById(data.roomId);
			}
		},
		async getRoomById(id) {
			await new Promise((resolve, reject) => {
				this.$kt.request.request({
					api: this.$api.business.ovo.chatRoom.getById,
					data: {
						id
					},
					stateSuccess: (res) => {
						this.room = res.data;
						this.$refs.scrollViewBox.getBodyData();
						uni.setStorageSync("roomId:" + this.room.roomId + ":room", this.room);
						resolve();
					}
				});
			});
		}
	}
}
</script>

<style lang="scss" scoped>
.box {
	padding: 0 30rpx 20rpx 30rpx;
	box-sizing: border-box;
}

.box-top {
	box-shadow: 0 30rpx 30rpx #f0f0f0;
	background-color: #fff;
}
</style>
